import { DefinitionIndeCanvasCore } from '../Core/DefinitionIndeCanvasCore';

import { TFProject } from '..';

// import * as tf from '@tensorflow/tfjs';
import { NVector3, MathUtility } from '../../../lib';
export class JSLinearRegressionGradientDescent02 extends DefinitionIndeCanvasCore {

    xpos: number = 100;
    ypos: number = 100;

    xspeed: number = 10;
    yspeed: number = 10;

    xdirection: number = 1;
    ydirection: number = 1;

    rad: number = 50;
    vs: NVector3[] = [];

    weight: number = 1;
    bias: number = 0;

    constructor(tfProjct: TFProject) {
        super(tfProjct);
        this.indeCanvas.title = 'LinearRegression';
        this.Init();
    }
    Init() {
        // console.log(tf.version);
    }
    MouseDown(x: number, y: number) {
        this.vs.push(new NVector3(this.MapX(x), this.MapY(y)));
        // console.log(this.vs);
    }
    MapX(v: number) {
        return MathUtility.Remap(v, 0, this.width, 0, 1);
    }
    MapY(v: number) {
        return MathUtility.Remap(v, 0, this.height, 1, 0);
    }
    RenderIndependentCanvas(ctx: CanvasRenderingContext2D) {
        ctx.fillStyle = '#000000';
        ctx.beginPath();
        ctx.rect(0, 0, ctx.canvas.width, ctx.canvas.height);
        ctx.closePath();
        ctx.fill();

        this.GradientDescent();
        if (this.vs.length > 1) {
            this.RenderLine(ctx);
        }

        ctx.fillStyle = '#00ff00';
        for (let i = 0; i < this.vs.length; ++i) {
            ctx.beginPath();
            ctx.arc(MathUtility.Remap(this.vs[i].x, 0, 1, 0, this.width),
                MathUtility.Remap(this.vs[i].y, 0, 1, this.height, 0), 5, 0, Math.PI * 2);
            ctx.closePath();
            ctx.fill();
        }
    }
    public RenderLine(ctx: CanvasRenderingContext2D) {
        let x0 = 0;
        let y0 = this.weight * x0 + this.bias;

        let x1 = 1;
        let y1 = this.weight * x1 + this.bias;

        ctx.lineWidth = 3;
        ctx.strokeStyle = '#00fff0';
        ctx.beginPath();
        ctx.moveTo(MathUtility.Remap(x0, 0, 1, 0, this.width), MathUtility.Remap(y0, 0, 1, this.height, 0));
        ctx.lineTo(MathUtility.Remap(x1, 0, 1, 0, this.width), MathUtility.Remap(y1, 0, 1, this.height, 0));
        ctx.stroke();
    }
    public GradientDescent() {
        let learningRate = 0.1;
        for (let i = 0; i < this.vs.length; ++i) {
            let x = this.vs[i].x;
            let y = this.vs[i].y;
            let hypothesis = this.weight * x + this.bias;
            let error = y - hypothesis;

            this.weight = this.weight + (error * x) * learningRate;
            this.bias = this.bias + (error) * learningRate;
            // console.log(error);
        }
    }
}